body {
  margin: 0;
  font-family: Arial, sans-serif;
  font-size: 100px;
}

.container {
  display: flex;
  flex-wrap: wrap;
}

.block {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 300px;
  min-width: 100%;
  color: white;
  text-align: center;
  background-color: rgb(255 0 0);
}

.block--2 {
  background-color: rgb(204 0 0);
}

.block--3 {
  background-color: rgb(153 0 0);
}

.block--4 {
  background-color: rgb(102 0 0);
}

.block--5 {
  background-color: rgb(51 0 0);
}

.block--6 {
  background-color: black;
}

@media (min-width: 600px) {
  .block {
    min-width: 50%;
  }

  .block--4 {
    order: 1;
  }

  .block--3 {
    order: 2;
  }

  .block--5 {
    order: 3;
  }

  .block--6 {
    order: 4;
  }
}

@media (min-width: 900px) {
  .block {
    min-width: 33.3%;
  }

  .block--3 {
    order: 0;
  }

  .block--6 {
    order: 1;
  }

  .block--5 {
    order: 2;
  }

  .block--4 {
    order: 3;
  }
}
